.navbarContainer {
  margin: 2rem auto;
  padding: 1rem 0;
  font-size: 2rem;
  width: 80vw;
  max-width: 100rem;
  position: relative;

  .navLinkContainer {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
    // top: 50%;
    // transform: translateY(-50%);
    transition: all .5s;
    opacity: 1;
  }

  @media only screen and (max-width: 490px) {
    .navLinkContainer {
      justify-content: space-around;
    }
  }

  .transformNavLinks {
    opacity: 0;
    transform: translateX(-100vw);
  }

  .toggleContainer {
    width: 80vw;
    max-width: 100rem;
    position: absolute;
    opacity: 0;
    top: 50%;
    // top: 50%;
    transform: translate(100vw, -50%);
    transition: all .5s;

    .toggle {
      width: 25rem;
      margin: auto;
      display: flex;
      justify-content: space-around;
      align-items: center;
    }
  }

  .transformToggle {
    opacity: 1;
    transform: translate(0vw, -50%);
  }

  .arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    font-size: 3rem;
    background-color: transparent;
    border: none;
    color: inherit;
    cursor: pointer;
  }

  .arrowRight {
    right: -10vw;
    margin-right: 1vw;
  }

  .arrowLeft {
    left: -10vw;
    margin-left: 1vw;
  }



  .borderWrapper {
    width: 15rem;
    height: 5rem;
    border-radius: 15px;
    background: linear-gradient(270deg, #FD6569 0%, #D354B4 47.75%, #AC44FB 95.98%);
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 2px 10px #222;
    transition: all .2s;

    &:hover {
      background: linear-gradient(270deg, darken(#FD6569, 10) 0%, darken(#D354B4, 10) 47.75%, darken(#AC44FB, 10) 95.98%);
      transform: translateY(-1px);
    }

    &:active {
      background: linear-gradient(270deg, darken(#FD6569, 10) 0%, darken(#D354B4, 10) 47.75%, darken(#AC44FB, 10) 95.98%);
      transform: translateY(1px);
    }

    .navLink {
      text-decoration: none;
      color: #fff;
      font-weight: 500;
      border-radius: 12px;
      background-color: #2b2b2b;
      width: 14rem;
      height: 4rem;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    .navLink-light {
      color: inherit;
      background-color: #efefef;
    }

    .randomBtn {
      width: 15rem;
      height: 5rem;
      background-color: transparent;
    }
  }
  .borderWrapper-light {
    box-shadow: 0 2px 10px #aaa;
  }
}

.react-switch {
  img {
    height: 100%;
    width: 100%;
  }
}