.page_404 {
    padding: 40px 0;
    background: #fff;
    font-family: 'Arvo', serif;
    position: absolute;
    width: 100%;
    max-width: 100rem;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.page_404 img {
    width: 100%;
}


.four_zero_four_bg {
    background: url(https://cdn.dribbble.com/users/285475/screenshots/2083086/dribbble_1.gif) no-repeat center center/contain;
    height: 50rem;
    background-position: center;
}

.four_zero_four_bg {

    h1 {
        font-size: 80px;
    }
    h3 {
        font-size: 80px;
    }
}

.link_404 {
    color: #fff !important;
    padding: 10px 20px;
    background: #39ac31;
    margin: 20px 0;
    display: inline-block;
    position: absolute;
    left: 0;
    bottom: 0;
    margin-left: 2rem;

}

.contant_box_404 {
    margin-top: -50px;
}